body {
  margin: 0;
  padding: 0;
  background: rgba(173, 209, 174, 0.5);
  font-family: 'Yanone Kaffeesatz', sans-serif;
  text-shadow: 1px 1px rgba(117, 117, 117, 0.5);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  text-decoration: none;
  font-weight: 600;
}
@media (min-width: 992px){
  body{
    width: 992px;
    margin: auto;
    font-size: x-large;
  }
}
.profPic{
  vertical-align: baseline;
  vertical-align: initial;
  height: 100px;
  width: 100px;
  margin: 2px;
  border-radius: 50%;
  border: 2px solid #f0f0f0;
  box-shadow: 3px 3px rgba(105, 105, 105, 0.5);
  float: left;
}
header{
  background: rgb(95, 193, 100);
  border-bottom: 2px solid rgb(95, 193, 100);
  padding-top: 5px;
}
.nav-lock{
  background: rgb(95, 193, 100);
  border-bottom: 2px solid rgba(240, 240, 240, .6);
  position: -webkit-sticky;
  position: sticky;
  padding-top: 5px;
  height: 20px;
  top: 0px;
  z-index: 1;
}
.intro{
  font-family: 'Merienda', cursive;
  color: white;
}
h1.intro{
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 110px;
}
h2.intro{
  margin-top: 0;
  margin-bottom: 35px;
  margin-left: 160px;
}
nav{
  display: flex;
  align-items: center;
  margin-bottom: 2px;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
}
.info{
  padding: 22px 10px;
  margin-bottom: 100px;
}
.indent{
  text-indent: 32px;
}
.project-title {
  font-family: 'Merienda', cursive;
  margin: 0;
  font-weight: bold;
}
.lead{
  margin-top: 3px;
}
.project-title:first-letter{
  font-weight: bold;
  font-size: 200%;
}
.nav-link{
  flex-grow:1;
  text-align: center;
  color: white;
  text-decoration: none;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.nav-link:hover{
  color: rgb(210,126,204);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  text-decoration: none;
}
@media (min-width: 992px){
  h2.intro{
    margin-left: 400px;
  }
  h1.intro{
    margin-left: 200px;
  }
  .profPic{
    height: 150px;
    width: 150px;
  }
  .info{
    margin-bottom: 200px;
  }
}
.sentence-link{
    color: white;
    text-decoration: none;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.sentence-link:hover{
    color: rgb(210,126,204);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    text-decoration: none;
}
.section{
    background: rgb(74, 119, 77);
    color: white;
}
.skills{
    display: flex;
    flex-wrap: wrap;
    min-width: 500px;
}
.group{
    background: #f0f0f0;
}
.skills li{
    flex-basis: 30%;
    margin: 5px;
}
.scroll{
    overflow-x: auto;
    margin-top: -15px;
}
@media (min-width: 992px){
    .skills li{
        flex-basis:32%;
    }
}
.proj{
    background: rgb(210,126,204);
}
.show-proj{
    width: 500px;
}
.proj-slide{
    width: 85%;
    margin: 15px auto;
}
.project-img{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: 2px solid #f0f0f0;
    box-shadow: 3px 3px rgba(105, 105, 105, 0.5);
    margin: 10px 10px 5px 5px;
    float: left;
}
h4{
    font-family: 'Merienda', cursive;
    margin-bottom: 2px;
    margin-top: 2px;
}
.proj-desc{
    margin: auto 5px;
}
.proj-hidden{
    display: none;
}
.proj-visible{
    display: block;
}
.more-btn{
    display: inherit;
    margin-top: 15px;
}
.less-btn{
    display: none;
}
.project-link{
    color: black;
    text-decoration: none;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.project-link:hover{
    color: rgb(95, 193, 100);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    text-decoration: none;
}
@media (min-width: 992px){
    .project-img{
        height: 150px;
        width: 150px;
    }
    .proj-hidden{
        display: block;
    }
    .more-btn{
        display: none;
    }
}
.extras{
    background: rgb(74, 119, 77);
    color: white;
}
.extra-link{
    color: white;
    text-decoration: none;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
.extra-link:hover{
    color: rgb(210,126,204);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    text-decoration: none;
}
.contact {
    background: #f0f0f0;
    margin-bottom:200px;
}
.contact-link{
    padding:5px;
    display: block;
    color: black;
    text-decoration: none;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    text-align: center;
}
.contact-link:hover{
    color: rgb(95, 193, 100);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    text-decoration: none;
}
.con-setup{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.contact-flex{
    flex-basis: 25%;
}
.contact-img{
    display: block;
    margin: auto;
}
